import {version} from '../package.json';

const configuration = {
    version: version,
    village: {
        sizeX: 3,
        sizeY: 3,
        starts: ['-1, 5', '-1, 6', '5, -1', '6, -1', '5, 12', '6, 12'],
        ends: ['12, 5', '12, 6'],
    },
    house: {
        sizeX: 4,
        sizeY: 4,
    },
};

export default configuration;



